#override-container .retrieve-content .inline-table-label {
	font-size: 1.2rem;
	margin-bottom: 0.25em;
	margin-top: 0.25em;
	font-weight: 900;
}

#override-container .retrieve-content .inline-table-label + .inline-table-caption {
	font-size: 1rem;
	margin-top: -0.5rem;
	padding-bottom: 0.5rem;
}

#override-container .breadcrumbs {
	line-height: 1;
	display: flex;
	text-align: left;
	align-items: start;
}

#override-container .breadcrumbs > span {
	max-width: 50%;
	overflow: hidden;
	display: block;
	text-overflow: ellipsis;
	flex: 0 1 auto;
	white-space: nowrap;
	overflow: hidden;
	min-width: 0;
	color: #6b6b6b;
}

#override-container .breadcrumbs > span + span:before {
	content: " > ";
	font-weight: 900;
	color: #d0d0d0;
	display: inline-flex;
	margin-left: 0.25em;
}

#override-container .modal-content .close {
	opacity: 1;
	margin-top: 0em;
}

#override-container .modal-content .close:hover {
	opacity: 0.9;
}

#override-container .retrieve-content figure img {
	display: none;
}

#override-container .dart-ads {
	display: flex;
}

#override-container .dart-ads .dart-ad {
	margin: 0 auto;
}

#override-container small-caps {
	text-transform: lowercase;
}

.gl-collapsefacet {
	display: none;
	-webkit-transition: all 0.1s ease-in-out;
	-moz-transition: all 0.1s ease-in-out;
	transition: all 0.1s ease-in-out;
}

.gl-expandfacet {
	display: block;
}

.gl-contentcollapse {
	height: 150px;
	overflow: hidden;
	width: 100%;
}

.gl-facet-col-collapse {
	width: 3% !important;
	-webkit-transition: all 0.1s ease-in-out;
	-moz-transition: all 0.1s ease-in-out;
	transition: all 0.1s ease-in-out;
}

.gl-tab-col-expand {
	width: 97% !important;
	-webkit-transition: all 0.1s ease-in-out;
	-moz-transition: all 0.1s ease-in-out;
	transition: all 0.1s ease-in-out;
}

.gl-facet-col-expand {
	-webkit-transition: all 0.1s ease-in-out;
	-moz-transition: all 0.1s ease-in-out;
	transition: all 0.1s ease-in-out;
}

.gl-tab-col-collapse {
	-webkit-transition: all 0.1s ease-in-out;
	-moz-transition: all 0.1s ease-in-out;
	transition: all 0.1s ease-in-out;
}

.gl-powered-by-logo {
	padding: 1em;
}

.gl-powered-by-logo img {
	float: right;
	width: 18%;
}

.gl-tablepreview {
	background: inherit;
	background-color: #e2edf1;
	border-radius: 0px;
	-moz-box-shadow: none;
	-webkit-box-shadow: none;
	box-shadow: none;
}

.gl-tablepreview .retrieve-content {
	border: 1px solid teal;
}

.gl-tablethumbnail {
	text-align: center;
	word-break: break-all;
}

.gl-tablethumbnail .fa {
	font-size: 80px;
	line-height: 1;
	text-align: center;
	margin: 0 auto;
	color: #908f8f;
}

.gl-tablethumbnail .gl-related-searches-link {
	text-decoration: none;
	line-height: 1.3em !important;
	display: block;
	text-align: left;
}

.COR_LOE {
	display: flex;
	white-space: nowrap;
	flex-direction: row;
}

.COR_LOE > *[class^="COR"],
.COR_LOE > *[class^="LOE"] {
	flex: 0 1 auto;
	color: black;
	font-weight: 900;
	text-align: center;
	padding: 0.5em 1em;
}

.COR-I {
	background-color: rgb(111, 194, 132) !important;
}

.COR-IIA {
	background-color: rgb(255, 212, 79) !important;
}

.COR-IIB {
	background-color: rgb(250, 167, 75) !important;
}

.COR-III,
.COR-III\:NOBENEFIT {
	background-color: rgb(241, 93, 76) !important;
}

.COR-III\:HARM {
	background-color: rgb(237, 28, 0) !important;
	color: white;
}

.LOE-A {
	background-color: rgb(62, 111, 183) !important;
	color: white;
}

.LOE-B-R {
	background-color: rgb(101, 156, 211) !important;
}

.LOE-B-NR {
	background-color: rgb(161, 193, 230) !important;
}

.LOE-C-LD,
.LOE-C-EO {
	background-color: rgb(181, 213, 229) !important;
}

.LOE-C,
.LOE-C-LD,
.LOE-C-EO {
	background-color: #b5d5e5 !important;
}

.LOE-B-R,
.LOE-B {
	background-color: #659cd3 !important;
}

.LOE-B-NR,
.LOE-B-NRSR {
	background-color: #a1c1e6 !important;
}

.gl-item-footer {
	width: 100%;
	padding: 0.5em;
	font-size: 0.75em;
	background-color: lightgray;
}

.gl-item-footer a {
	font-weight: bold;
}

.gl-item-footer a:hover {
	text-decoration: none;
}

.coveo-omnibox .coveo-omnibox-selectable:hover {
	background-color: #d7ebff;
}